.skils{
    margin: 0%;
    padding: 0%;
     -webkit-box-sizing: border-box;
     box-sizing: border-box;
     background: rgb(2,0,36);
     background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(98,98,185,1) 35%, rgba(0,212,255,1) 100%);
}


body{
    font-family: "popins", sans-serif
}


.container{
    width: 100%;
    padding-right:20px;
    padding-left:20px;
    margin-right:auto;
    margin-left: auto;
    height: 100%;

}

.bg9{
    
    background-image: url('../../images/bg9.avif');
    z-index: -1;
    height: 680px;
    background-repeat: no-repeat;
    background-position-x: center;
    background-size: 100% 100%;
    padding:1rem;


}
@media (min-width :768px){
    .container{
        margin:0 auto;
    }
}
@media(min-width:992px){
    .container{
        max-width :960px;
    }
}
@media (min-width:1200px){
    .container{
        max-width: 1140px;
    }
}
@media (min-width:1400px){
    .container{
        max-width: 1350px;
    }
}

.section_title p{
    font-weight: 500;
    font-size: 22px;
    line-height: 1.5;
    color: #fd6e0a;
}
.section_title h2{
    font-weight :700;
    font-size: 30px;
    line-height: 1.5;
    text-transform: capitalize;
    color:#15295f;
}
.section_title.center{
    text-align:center;
}
.experience-section{
    padding: 65px 0 100px;
    position:relative;
}
.experience-section .experience-items{
    margin-top:30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display:flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    gap:20px;
}
.experience-item{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 48%;
    flex: 0 0 48%;
    max-width: 48%;
}
.experience-info{
    display: -webkit-box;
    display: -ms-flexbox;
    display:flex;
    -webkit-box-pack:justify;
    -ms-flex-pack:justify;
    justify-content: space-between;
}
.experience-info p{
    font-weight: 600;
    font-size: 16px;
    line-height:22px;
    color:#15295f;
    opacity: 1;
    -webkit-animation: showText 0.5s 1s linear forwards;
    animation: showText 0.5s 1s linear forwards;
}
.progress-line{
    margin-top: 7px;
    height:10px;
    width:100px;
    background-color: #cdcdcd;
    border-radius:50px;
    position:relative;
}
.progress-line span {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: left;
    transform-origin: left;
    -webkit-animation: animate 2s cubic-bezier(1,0,0.5,1) forwards;
    animation: animate 1s cubic-bezier(1,0, 0.5,1) forwards;
    height:100%;
    border-radius: 50px;
    position :absolute;
    background-color: #fd6e0a;
    transition: 2s all;
}
.progress-line span.animate{
    -webkit-animation: animate 2s cubic-bezier(1,0,0.5,1) forwards;
    animation: animate 1s cubic-bezier(1,0, 0.5,1) forwards;
}
@-webkit-keyframes showText{
    100% {
        opacity: 1;
    }
}
@keyframes showText{
    100%{
        opacity: 1;
    }
}
@keyframes animate {
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}
@keyframes animate {
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}

@media only screen and (max-width: 800px) {
    .skill {
      font-size: var(--section-heading-size-mobile) !important;
    }
    .skill {
      flex-direction: column;
    }
    
    .skill-right,
    .skill-left {
      width: 100% !important;
    }
    .skill-left > p {
      margin-bottom: 50px;
    }

    .skills {
        font-family: "Kalam", cursive;
        font-weight: 300;
        font-style: normal;
      }
      
      .skills {
        font-family: "Kalam", cursive;
        font-weight: 400;
        font-style: normal;
      }
      
      .skills{
        font-family: "Kalam", cursive;
        font-weight: 700;
        font-style: normal;
      }
  }