.resume{
    margin: 0%;
    padding: 0%;
     -webkit-box-sizing: border-box;
     box-sizing: border-box;
     align-items: center;
     display: flex;
     background: radial-gradient(circle at 100%, #333, #333 50%, #eee 75%, #333 75%);

}
.rm{
     background-image: url('../../images/rm.png');
     z-index: -1;
     height: 680px;
     background-repeat: no-repeat;
     background-position-x: center;
     background-size: cover; 

}
.Card{
    

  margin-right: auto; 
  margin-left: auto; 
  width: 250px; 
  box-shadow: 0 15px 25px rgba(233, 233, 238, 0.937); 
  height: 300px; 
  border-radius: 5px; 
  backdrop-filter: blur(14px); 
  background-color: rgba(0, 5, 8, 0.2); 
  padding: 10px; 
  text-align: center; 
  display: grid;




}

.card-img-top{
  height:200px;
}

.card-body{
  height: 0;
  backdrop-filter: blur(14px); 
  
}




 
.card {
  
  width:70%;

}

   
   
   .img{
     width:12rem ;
     height: 80%;
     background-image: url('../../images/bg10.avif');

   }
   .card-body{
     padding:4rem;
     background-color: rgb(243, 242, 244);
   }

   @media only screen and (max-width: 550px) {
    .resume {
      font-size: var(--section-heading-size-mobile) !important;
    }
    .resume {
      font-size: 4.5rem !important;
    }
  }
  .resume {
    font-family: "Kalam", cursive;
    font-weight: 300;
    font-style: normal;
  }
  
  .resume .card-title {
    font-family: "Kalam", cursive;
    font-weight: 400;
    font-style: normal;
  }
  
  .resume .card-body{
    font-family: "Kalam", cursive;
    font-weight: 700;
    font-style: normal;
  }
  @media screen and (max-width:768px) {
    img {
        width:100%;
    }
    .Card {
        max-width:100%;
        width: 100%;
        align-items: center;
    }
    .col{
        width:100%;
        float:center;
        margin:1;
    }
    }