.contact{
    position: relative;
    height:100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    &__personalWrapper, &__serviceWrapper{
        width: 50%;
    }
}


.Container {
    display: flex;
    flex-direction: column;
    margin: 50px auto;
    background-color: rgb(224, 234, 238);
    padding: 10px;
    width: 300px;
    height: 410px;
    align-items: center;
    border-radius: 10px;
    box-shadow: 1px 1px 8px rgb(61, 134, 162) ;

}
