
.home {
    position: relative ;
    height: 100%;
    display: flex;
    padding: 10 rem;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    text-align: left;
    font-family: "Sofia, sans-serif"; 
    &__personalWrapper, &__serviceWrapper{
        width: 100%;
    }
    
}

 .section.home__text-wrapper {
    font-weight: 700;
    font-size: 30px;
    line-height: 1.5;
    text-transform: capitalize;
    color: #15295f;
    h2 {
        text-shadow: 0 0 3px #15295f;
        
      }

}


.bg11{
    background-image: url('../../images/bg11.avif');
    z-index: -1;
    height: 680px ;
    background-repeat: no-repeat;
    background-position-x: center;
    background-size: cover;

}


 

  @media only screen and (max-width: 7680px) {
    .home{
      flex-direction: column;
      align-items: center;
      width:100%;
      .h2_text-muted {
        align-items: center;
        padding-left: 10px;
      }
      
    }
   
    .home {
      width: 50%;
      align-items: center;
    }
    
  }
  .home {
    font-family: "Kalam", cursive;
    font-weight: 300;
    font-style: normal;
  }
  
  .home {
    font-family: "Kalam", cursive;
    font-weight: 400;
    font-style: normal;
  }
  
  .home{
    font-family: "Kalam", cursive;
    font-weight: 700;
    font-style: normal;
  }
  .home {
    max-width:100%;
    width: 100%;
    align-items: center;
}
  