
.navbar {

height:80px;
display:flex;
justify-content: center;
align-items: center;
font-size: 1.2rem;

&__container{
    display: flex;
    align-items: center;
    width: 100%;
    height:inherit;
    padding: 0px, 20px;
    flex: 1;

    &__logo{
      cursor: pointer;
      align-items: left;
    }
  &__menu{
    display: flex;
    list-style: none;
    text-align: center;

    
&__item{
    
    margin-right: 1.5rem;
    font-size: 1.2rem;
    text-decoration: wavy;


    &::after{
        content: '';
        display:block;
        height:3px;
        width:0;
        background: transparent;
        transition: all 0.5secs ease;
    }



&:hover::after{
    width: 100%;
    background: transparent;
}
&_links{
    color: rgb(224, 234, 234);
    padding: 0.5rem;
    text-decoration: none;
}
}

  }

}
}

.nav-icon{
    display:none;
}



@media only screen and (max-width: 768px) {
    .navbar__container__menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      position:absolute;
      top: 80px;
      left: -110%;
      opacity: 1;
      align-items: center;
      transition:all 0.5s ease ;

      &.active{
        
        backdrop-filter: none;
        left: 0px;
        opacity: 1;
        
      
      }
    
    
      &_item{
        border-bottom: 1px solid #9fc2d4;
        margin:0px;
        padding:10px ;
      }

    }
    .nav-icon{
      display: flex;
      cursor: pointer;
      color:rgba(16, 16, 17, 0.677);
      padding-right: 20px;
    }

    
    
    .navbar {
      font-family: "Kalam", cursive;
      font-weight: 400;
      font-style: normal;
    }
    
    .navbar{
      font-family: "Kalam", cursive;
      font-weight: 500;
      font-style: normal;
    }

    .navbar {
      width: 100%;
      align-items: center;
  }
   
  }