.about{
    position: relative;
    height:100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    &__personalWrapper, &__serviceWrapper{
        width: 50%;
    }



}
.bgi {
    background-image: url('../../images/bgi.avif');
    z-index: -1;
height: 680px;
    background-repeat: no-repeat;
    background-position-x: center;
    background-size: cover;

  }
  @media only screen and (max-width: 800px) {
    .about{
      flex-direction: column;
    }
    .about {
      font-size: var(--section-heading-size-mobile) !important;
    }
    
    .about {
      width: 100% !important;
    }
    .about {
      margin-bottom: 50px;
    }
  }
  .about {
    font-family: "Kalam", cursive;
    font-weight: 300;
    font-style: normal;
  }
  
  .about {
    font-family: "Kalam", cursive;
    font-weight: 400;
    font-style: normal;
  }
  
  .about {
    font-family: "Kalam", cursive;
    font-weight: 700;
    font-style: normal;
  }